<template>
    <div class="content">
        <el-row :class="{'row-bg': true}" type="flex">
            <el-steps direction="vertical" :space="size" :class="{'stepper': true}" :active="active" finish-status="success">
                <el-step title="Intro"></el-step>
                <el-step title="Lanche"></el-step>
                <el-step title="Outros"></el-step>
                <el-step title="Confirmar"></el-step>
            </el-steps>
            <!--el-steps direction="vertical" :space="size" :active="active" finish-status="success" >
                <el-step title="Intro" :class="{'stepper': true}"></el-step>
                <el-step title="Lanche" :class="{'stepper': true}"></el-step>
                <el-step title="Outros"></el-step>
                <el-step title="Confirmar"></el-step>
            </el-steps-->
            <component :is="steps"></component>
            <el-col>
                <div>
                    <el-button :class="{btnLanches: true}" @click="back">Voltar</el-button>
                    <el-button :class="{btnLanches: true, 'blue-bg': true}" @click="next">Continuar</el-button>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    import Step1 from './Lanches/Step1';
    import Step2 from './Lanches/Step2';
    import Step3 from './Lanches/Step3';
    import Step4 from './Lanches/Step4';

    export default {
        data() {
            return {
                active: 0,
                size: 125
            }
        },
        computed: {
            steps() {
                return 'Step' + (this.active + 1);
            }
        },
        methods: {
            next() {
                if (this.active++ > 3) this.active = 0;
            },
            back() {
                if (this.active-- < 1) this.active = 0;
            }
        },
        components: {
            Step1,
            Step2,
            Step3,
            Step4
        }
    }
</script>
<style scoped>
    .stepper {
        margin-left: 2vw;
        width: 50vw;
    }
    .content {
        height: 100vh;
        display: flex;
        justify-content: center;
    }

    .row-bg {
        flex-direction: row;
        margin-top: 3vh;
        align-items: center;
    }

    .btnLanches {
        margin-top: 12px;
        font-family: 'Roboto', sans-serif;
    }
    
</style>


